.cloud_notifications_item {
    background-color: #333333;
    padding: 8px;
    border-radius: 8px;
    color: #AFAFAF;
    margin: 8px;
    text-align: left;
    font-weight: 600;
    font-size: small;
    width: 250px;   
}

.cloud_notifications_item_header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;  
}


.cloud_notifications_item_header_text {
    max-width: 200px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    display: inline-block;
    border: none;
    background-color: transparent;
    color: inherit;
    font: inherit;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

.cloud_notifications_item_header_text:hover {
    text-decoration: underline;
}

.cloud_notifications_item_header_close {
    background-color: transparent;
    border: none;
    color: inherit;
    font: inherit;
    cursor: pointer;
    border-radius: 50px;
    padding: 0;
    height: 25px;
    width: 25px;
}

.cloud_notifications_item_header_close:hover {
    color: #CBCBCB;
    background-color: #555555;
}

.cloud_notifications_item_body {
    max-height: 0;
    overflow-y: auto;
    word-wrap: break-word;
    -webkit-transition: max-height .2s ease, margin-top .3s ease;
    -o-transition: max-height .2s ease, margin-top .3s ease;
    transition: max-height .2s ease, margin-top .3s ease;
}

.cloud_notifications_item_body * {
    margin: 0;
}

.cloud_notifications_item[aria-expanded="true"] .cloud_notifications_item_body {
    max-height: 150px;
    margin-top: 8px;
}


