.filestable_header_select {
  text-align: center;
  width: 50px;
  line-height: 0;
  white-space: nowrap;
  cursor: pointer;
  border-right: 1px solid #ccc;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.filestable_header_name {
  -ms-flex-positive:20;
      flex-grow:20;
  -ms-flex-negative: 20;
      flex-shrink: 20;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  text-align: left;
  padding-left: 16px;
}

.filestable_header_icons {
  white-space: nowrap;
}

.filestable_header_lastupdate {
  width: 150px;
  text-align: left;
  white-space: nowrap;
}

.filestable_header_size {
  width: 70px;
  text-align: left;
  white-space: nowrap;
}

.filestable_content {
  padding: 0;
  margin: 0;
  /*height: 230px;  TODO: dynamic height using JS */
  overflow-y: auto;
}


